body {
    box-sizing: border-box;
}

.ageInput {
    width: 100%;
    padding: 5px;
}


.ageBtn {
    display: block;
    margin: 10px auto;
    padding: 10px 20px;
    border: 1px solid black;
    border-radius: 20px;
    transition: all .3s ease-in-out;
    cursor: pointer;

}

h2, h3 {
    text-align: center;
}

.right {
    display: block;
    margin: 10px auto;
    padding: 8px 20px;
    border-radius: 20px;
    border: 1px solid black;
    cursor: pointer;
    transition: all .3s ease-in-out;


}

.wrong {
    display: block;
    margin: 8px auto;
    padding: 8px 20px;
    border-radius: 20px;
    cursor: pointer;
    border: 1px solid black;
    transition: all .3s ease-in-out;
}

.wrong:hover,.right:hover, .ageBtn:hover {
    background-color: black;
    color: white;
}

